import React from 'react';

const TableHeader = () => {
    return (
        <thead>
            <tr>
                <th>Name</th>
                <th>Job</th>
                <th>Remove</th>
                <th></th>
            </tr>
        </thead>
    );
}

const TableBody = (props) => {
    const editButton = () => {
        alert("Edit Button is clicked");
    }
    const rows = props.characterData.map((row, index) => {
        return (
            <tr key={index}>
                <td>
                    {row.name}
                </td>
                <td>
                    {row.job}
                </td>
                <td>
                    <button onClick={() => props.removeCharacter(index)}>Delete</button>
                </td>
                <td>
                    <button  class="icon-edit" onClick={editButton}><img alt="" src="https://image.flaticon.com/icons/svg/66/66761.svg"/></button>
                    <button  class="icon-cancel"><img alt="" src="https://image.flaticon.com/icons/svg/66/66847.svg"/></button>
                    <button  class="icon-save"><img alt="" src="https://www.flaticon.com/premium-icon/icons/svg/2767/2767140.svg"/></button>
                </td>
            </tr>
        );
    });
    return <tbody>{rows}</tbody>;
}

const Table = (props) => {
    
    const {characterData,removeCharacter} = props
    return(
            <table>
                <TableHeader />
                <TableBody characterData={characterData} removeCharacter={removeCharacter}/>
            </table>
        );
}

export default Table;